import { Component, ReactNode } from "react";
import style from './Task.module.css';
import Localization from "../../utils/localization/localization";
import { TaskInfo } from "./TaskContainer";
import Task from "./Task";
import React from "react";

type Props = {
    items: TaskInfo[]
}

class TasksPanel extends Component<Props> {
    localization = Localization.getLocalization("tasks");
    tasksRef = React.createRef<HTMLDivElement>();
    state = { isMinimized: true };

    componentDidMount() {
        document.addEventListener("mousedown", this.handleClickOutside);
    }
    
    componentWillUnmount() {
        document.removeEventListener("mousedown", this.handleClickOutside);
    }

    handleClickOutside = (event: MouseEvent): void => {
        if (!this.state.isMinimized && !this.tasksRef?.current?.contains(event.target as Node)) {
            this.toggleMinimized();
        }
    }

    toggleMinimized = (): void => {
        this.setState({ 
            isMinimized: !this.state.isMinimized 
        }); 
    }

    render(): ReactNode  {
        return (
            <div className={style.tasksPanel}  ref={this.tasksRef}>
                <div className={style.tasksPanelMinimized} onClick={this.toggleMinimized}>                    
                    <b>{this.localization("header")}</b>
                </div>

                {
                    !this.state.isMinimized &&
                        <div className={style.tasksContainer}>                            
                            {
                                this.props.items.map(item => <Task key={item.id} item={item} />)
                            }
                        </div>
                }
            </div>
        );
    }
}

export default TasksPanel;