import { Component, ReactNode } from "react";
import style from './UnitPrice.module.css';
import { ModalMenuOption } from "../common/modalMenu/ModalMenu";
import { HiBan, HiCheck } from 'react-icons/hi';
import { UnitPriceSpecificationInfo, UnitPriceSpecificationRequest } from "../../store/types/unit-price/UnitPriceInfo";

type Props = {
    unitPriceId: number,
    specificationItems: UnitPriceSpecificationInfo[],
    onContextMenu: (top: number, left: number, options: Array<ModalMenuOption>) => void,
    createUnitPriceSpecification: (unitPriceId: number, specification: UnitPriceSpecificationRequest) => void,
    deleteUnitPriceSpecification: (unitPriceId: number, id: number) => void
}

class UnitPriceSpecification extends Component<Props> {
    state = {
        isAddMode: false
    };

    indexes = ["А","Б","В","Г","Д","Е","Ё","Ж","З","И","Й","К","Л","М",
        "Н","О","П","Р","С","Т","У","Ф","Х","Ц","Ч","Ш","Щ","Ю","Я"];

    onContextMenu = (event: React.MouseEvent<HTMLElement>) => {
        event.preventDefault();

        const options : Array<ModalMenuOption> = [
            {name: "Добавить описание", onClick: this.modalMenuOnInsert}
        ];

        this.props.onContextMenu(event.pageY, event.pageX, options);
    }

    modalMenuOnInsert = () => {
        this.setState({isAddMode: true});
    }

    onNewItemSubmit = (request: UnitPriceSpecificationRequest) => {
        this.props.createUnitPriceSpecification(this.props.unitPriceId, request);
        this.setState({isAddMode: false});
    }

    onNewItemCancel = () => {
        this.setState({isAddMode: false});
    }

    render(): ReactNode {
        let index = 0;
        return (
            <div className={style.unitPriceSpecification}>
                <div className={style.unitPriceSpecificationHeader}>
                    <span>1. Описательная часть</span>
                </div>
                <div className={style.upeSummary}>
                    <div className={style.upeSummaryHeader} onContextMenu={this.onContextMenu}>
                        <div className={style.upsCode}>Код</div>
                        <div className={style.upsName}>Название</div>
                        <div className={style.upsDescription}>Описание</div>
                    </div>
                    {
                    this.props.specificationItems.map(specification => 
                        <UnitPriceSpecificationItem 
                            key={specification.id}
                            deleteUnitPriceSpecification={this.props.deleteUnitPriceSpecification}
                            onContextMenu={this.props.onContextMenu}
                            modalMenuOnInsert={this.modalMenuOnInsert}
                            specification={specification} 
                            index={this.indexes[index++]} />)
                }
                {
                    this.state.isAddMode &&
                        <AddUnitPriceSpecificationItem 
                            onNewItemSubmit={this.onNewItemSubmit}
                            onCancel={this.onNewItemCancel}
                            index={this.indexes[index++]} />
                }
                </div>
            </div>
        );
    }
}


type Props2 = {
    index: string,
    onNewItemSubmit: (request: UnitPriceSpecificationRequest) => void,
    onCancel: () => void
}

class AddUnitPriceSpecificationItem extends Component<Props2>{
    state = {
        name: "",
        description: "",
    }

    onSubmit = () => {
        const request: UnitPriceSpecificationRequest = {
            name: this.state.name,
            description: this.state.description
        }
        
        this.props.onNewItemSubmit(request);
    }

    onNameChange = (event: React.ChangeEvent<HTMLInputElement>) => this.setState({name: event.target.value});

    onDescriptionChange = (event: React.ChangeEvent<HTMLInputElement>) => this.setState({description: event.target.value});

    render(): ReactNode {
        return (            
            <div className={style.upeSummaryData}>
                <div className={style.upsCode}>{this.props.index}</div>
                <div className={style.upsName}>
                    <input className={style.upeSummaryNameInput} placeholder="Название" value={this.state.name} onChange={this.onNameChange} type={"text"} />
                </div>
                <div className={style.upsDescription}>
                    <input className={style.upeSummaryNameInput} placeholder="Описание" value={this.state.description} onChange={this.onDescriptionChange} type={"text"} />
                </div>
                <div className={style.upeControlPanel}>
                    <HiBan onClick={this.props.onCancel} className={style.upeControlPanelIcon}/>
                    <HiCheck onClick={this.onSubmit} className={style.upeControlPanelIcon} />
                </div>
            </div>
        );
    }
}

type Props1 = {
    deleteUnitPriceSpecification: (unitPriceId: number, id: number) => void,
    onContextMenu: (top: number, left: number, options: Array<ModalMenuOption>) => void,
    modalMenuOnInsert: () => void,
    specification: UnitPriceSpecificationInfo,
    index: string
}

class UnitPriceSpecificationItem extends Component<Props1>{
    onContextMenu = (event: React.MouseEvent<HTMLElement>) => {
        event.preventDefault();

        const options : Array<ModalMenuOption> = [
            {name: "Добавить описание", onClick: this.props.modalMenuOnInsert},
            {name: "Редактировать описание", onClick: () => {}},
            {name: "Удалить описание", onClick: this.onDelete}
        ];

        this.props.onContextMenu(event.pageY, event.pageX, options);
    }

    onDelete = () => {
        this.props.deleteUnitPriceSpecification(this.props.specification.unitPriceId, this.props.specification.id);
    }


    render(): ReactNode {
        const specification = this.props.specification;
        return (
            <div className={style.upeSummaryData} onContextMenu={this.onContextMenu}>
                <div className={style.upsCode}>{this.props.index}</div>
                <div className={style.upsName}>{specification.name}</div>
                <div className={style.upsDescription}>{specification.description}</div>
            </div>
        );
    }
}


export default UnitPriceSpecification;