import { UnitPriceActions } from "../actions/unit-price-actions";
import { UnitPriceInfo, UnitPriceResourceInfo, UnitPriceSpecificationInfo  } from "../types/unit-price/UnitPriceInfo";

export const ADD_UNIT_PRICE_ACTION = "UNIT_PRICES/ADD_UNIT_PRICE";
export const SET_UNIT_PRICES_ACTION = "UNIT_PRICES/SET_UNIT_PRICES";
export const SET_ACTIVE_UNIT_PRICE_ACTION = "UNIT_PRICES/SET_ACTIVE_UNIT_PRICE_ACTION";
export const ADD_ACTIVE_UNIT_PRICE_RESOURCE_ACTION = "UNIT_PRICES/ADD_ACTIVE_UNIT_PRICE_RESOURCE";
export const DELETE_ACTIVE_UNIT_PRICE_RESOURCE_ACTION = "UNIT_PRICES/DELETE_ACTIVE_UNIT_PRICE_RESOURCE";
export const ADD_ACTIVE_UNIT_PRICE_SPECIFICATION_ACTION = "UNIT_PRICES/ADD_ACTIVE_UNIT_PRICE_SPECIFICATION";
export const DELETE_ACTIVE_UNIT_PRICE_SPECIFICATION_ACTION = "UNIT_PRICES/DELETE_ACTIVE_UNIT_PRICE_SPECIFICATION";
export const UPDATE_UNIT_PRICE_ACTION = "UNIT_PRICES/EDIT_UNIT_PRICE";
export const REMOVE_UNIT_PRICE_ACTION = "UNIT_PRICES/DELETE_UNIT_PRICE";

let initialState = {
    unitPrices: [] as Array<UnitPriceInfo>,
    activeUnitPrice: {} as UnitPriceInfo,
    resources: [] as Array<UnitPriceResourceInfo>,
    specifications: [] as Array<UnitPriceSpecificationInfo>,
};

const unitPricesReduser = (state = initialState, action: UnitPriceActions): typeof initialState => {
    switch (action.type) {
        case ADD_UNIT_PRICE_ACTION:            
            return {...state, unitPrices: [...state.unitPrices, action.unitPrice]};

        case SET_UNIT_PRICES_ACTION:
            return {...state, unitPrices: action.unitPrices};

        case SET_ACTIVE_UNIT_PRICE_ACTION:
            return {...state, activeUnitPrice: action.unitPrice, resources: action.unitPrice.resources, specifications: action.unitPrice.specifications};

        case ADD_ACTIVE_UNIT_PRICE_RESOURCE_ACTION:
            return {...state, resources: [...state.resources, action.resource]};

        case DELETE_ACTIVE_UNIT_PRICE_RESOURCE_ACTION:
            return {...state, resources: state.resources.filter(resource => resource.id !== action.resource.id)};

        case ADD_ACTIVE_UNIT_PRICE_SPECIFICATION_ACTION:
            return {...state, specifications: [...state.specifications, action.specification]};

        case DELETE_ACTIVE_UNIT_PRICE_SPECIFICATION_ACTION:
            return {...state, specifications: state.specifications.filter(spec => spec.id !== action.specification.id)};

        case UPDATE_UNIT_PRICE_ACTION:
            var unitPrices = [...state.unitPrices];
            var index = unitPrices.findIndex(p => p.id === action.unitPrice.id);
            unitPrices[index] = action.unitPrice;
            return {...state, unitPrices: [...unitPrices]};
    
        case REMOVE_UNIT_PRICE_ACTION:
            return {...state, unitPrices: state.unitPrices.filter(p => p.id !== action.id)};

        default:
            return state;
    }
}

export default unitPricesReduser;