import { connect } from "react-redux";
import { AppStore } from "../../store/redusers/redux-store";
import UnitPriceCalculation from "./UnitPriceCalculation";
import { compose } from "redux";
import { withRouter } from "react-router-dom";
import { createUnitPriceResource, createUnitPriceSpecification, deleteUnitPriceResource, deleteUnitPriceSpecification, getUnitPriceById } from "../../store/actions/unit-price-actions";

const mapState = (store: AppStore) => {
    return ({
        item: store.unitPricesData.activeUnitPrice,
        resourceItems: store.unitPricesData.resources,
        specificationItems: store.unitPricesData.specifications
    });
}

export default compose<React.ComponentType>(
    connect(mapState, { getUnitPriceById, createUnitPriceResource, deleteUnitPriceResource, createUnitPriceSpecification, deleteUnitPriceSpecification }),
    withRouter
)(UnitPriceCalculation);