import { Component, ReactNode } from "react";
import style from "./Wor.module.css";
import { RouteComponentProps } from "react-router-dom";
import { WorItemInfo } from "./WorContainer";

type Props = {
    getWor: (id: number) => void,
    items: Array<WorItemInfo>
}

type RouteParams = {
    id: string
}

class Wor extends Component<Props & RouteComponentProps<RouteParams>> {   
    projectId = Number(this.props.match.params.id);

    componentDidMount() {
        this.props.getWor(this.projectId);
    }

    render(): ReactNode {
        return (
            <div className={style.structureContainer}>
                <div className={style.structureHeader}>
                    <div className={style.item} style={{width: 100}}>
                        <span>Группа ЕР</span>
                    </div>
                    <div className={style.item} style={{width: 200}}>
                        <span>Дисциплина</span>
                    </div>
                    <div className={style.item} style={{width: 120}}>
                        <span>Шифр ЕР</span>
                    </div>
                    <div className={style.item} style={{width: 500}}>
                        <span>Наименование ЕР</span>
                    </div>
                    <div className={style.item} style={{width: 70}}>
                        <span>Ед. изм.</span>
                    </div>
                    <div className={style.item} style={{width: 100}}>
                        <span>Объем</span>
                    </div>
                    <div className={style.item} style={{width: 100}}>
                        <span>Цена за ед.</span>
                    </div>
                    <div className={style.item} style={{width: 100}}>
                        <span>Стоимость</span>
                    </div>
                </div>
                {
                    this.props.items.map(item => <WorItem key={item.code} item={item} />)
                }
            </div>
        );
    }
}

type Props1 = {
    item: WorItemInfo
}

class WorItem extends Component<Props1> {
    render(): ReactNode {
        const item = this.props.item;

        return (
            <div className={style.structureItem}>                
                <div className={style.item} style={{width: 100}}>
                    <span>{item.baseCode}</span>
                </div>
                <div className={style.item} style={{width: 200}}>
                    <span>{item.subject}</span>
                </div>
                <div className={style.item} style={{width: 120}}>
                    <span>{item.code}</span>
                </div>
                <div className={style.item} style={{width: 500}}>
                    <span>{item.name}</span>
                </div>
                <div className={style.item} style={{width: 70}}>
                    <span>{item.units}</span>
                </div>
                <div className={style.item} style={{width: 100}}>
                    <span>{item.volume}</span>
                </div>
                <div className={style.item} style={{width: 100}}>
                    <span>{item.pricePerUnit}</span>
                </div>
                <div className={style.item} style={{width: 100}}>
                    <span>{item.price}</span>
                </div>
            </div>
        );
    }
}

export default Wor;