import { Component, ReactNode } from "react";
import { RouteComponentProps } from "react-router-dom";
import style from './UnitPrice.module.css';
import { UnitPriceInfo, UnitPriceResourceInfo, UnitPriceResourceRequest, UnitPriceSpecificationInfo, UnitPriceSpecificationRequest } from "../../store/types/unit-price/UnitPriceInfo";
import UnitPriceSummary from "./UnitPriceSummary";
import UnitPriceSpecification from "./UnitPriceSpecification";
import ModalMenu, { ModalMenuOption } from "../common/modalMenu/ModalMenu";
import { StaticContext } from "react-router";

type Props = {
    item: UnitPriceInfo,
    specificationItems: UnitPriceSpecificationInfo[],
    resourceItems: UnitPriceResourceInfo[],
    getUnitPriceById: (id: number) => void,
    createUnitPriceResource: (unitPriceId: number, resource: UnitPriceResourceRequest) => void,
    deleteUnitPriceResource: (unitPriceId: number, id: number) => void,
    createUnitPriceSpecification: (unitPriceId: number, resource: UnitPriceSpecificationRequest) => void,
    deleteUnitPriceSpecification: (unitPriceId: number, id: number) => void
}

type RouteParams = {
    id: string
}

class UnitPriceCalculation extends Component<Props & RouteComponentProps<RouteParams>> {
    state = {
        modalTop: 0,
        modalLeft: 0,
        modalOptions: [] as Array<ModalMenuOption>,
        isModalMenuVisible: false,
        summaryWorkerLaborCost: 0,
        summaryMachineLaborCost: 0
    };

    unitPriceId = Number(this.props.match.params.id);

    componentDidMount(): void {
        this.props.getUnitPriceById(this.unitPriceId);
        document.addEventListener("click", this.handleLeftClick);
    }

    componentDidUpdate(): void {
        const worker = this.props.resourceItems?.reduce((sum, p) => sum + p.workerLaborCostPerHour, 0) ?? 0;
        const machine = this.props.resourceItems?.reduce((sum, p) => sum + p.machineLaborCostPerHour, 0) ?? 0;
        if (this.state.summaryMachineLaborCost !== machine || this.state.summaryWorkerLaborCost !== worker) {
            this.setState({
                summaryWorkerLaborCost: worker,
                summaryMachineLaborCost: machine
            })
        }
    }
    
    componentWillUnmount() {
        document.removeEventListener("click", this.handleLeftClick);
    }

    onContextMenu = (top: number, left: number, options: Array<ModalMenuOption>) => {
        this.setState({
            modalTop: top,
            modalLeft: left,
            modalOptions: options,
        })
        
        this.setState({isModalMenuVisible: true});
    }

    handleLeftClick = (event: MouseEvent) => {
        this.setState({isModalMenuVisible: false});
    }

    render(): ReactNode {
        const item = this.props.item;
        return (
            <div className={style.unitPriceContainer}>
                {
                    this.state.isModalMenuVisible &&
                        <ModalMenu 
                            top={this.state.modalTop} 
                            left={this.state.modalLeft} 
                            options={this.state.modalOptions} />
                }
                <div>
                    <h3>Калькуляция затрат единичной расценки</h3>
                </div>
                <div className={style.unitPriceExpenses}>
                    <div className={style.upeHeader}>
                        <div className={style.upeHeaderTitle}>
                            <span>Сводка затрат</span>
                        </div>
                        <div className={style.upeHeaderCalculation}>
                            <span>Калькуляция</span>
                        </div>
                    </div>
                    <div className={style.upeSummary}>
                        <div className={style.upeSummaryHeader}>
                            <div className={style.upeSummaryCode}>Шифр ЕР</div>
                            <div className={style.upeSummaryName}>Наименование ЕР</div>
                            <div className={style.upeSummaryUnits}>Ед.изм.</div>
                            <div className={style.upeSummaryWorkerLaborCost}>ТЗР, чел.-час.</div>
                            <div className={style.upeSummaryMachineLaborCost}>ТЗМ, маш.-час.</div>
                        </div>
                        <div className={style.upeSummaryData}>
                            <div className={style.upeSummaryCode}>{item.code}</div>
                            <div className={style.upeSummaryName}>{item.name}</div>
                            <div className={style.upeSummaryUnits}>{item.unit?.code}</div>
                            <div className={style.upeSummaryWorkerLaborCost}>{this.state.summaryWorkerLaborCost}</div>
                            <div className={style.upeSummaryMachineLaborCost}>{this.state.summaryMachineLaborCost}</div>
                        </div>
                    </div>
                    <div className={style.spacer}></div>
                    <UnitPriceSummary
                        unitPriceId={this.props.item.id}
                        resourceItems={this.props.resourceItems}
                        createUnitPriceResource={this.props.createUnitPriceResource}
                        deleteUnitPriceResource={this.props.deleteUnitPriceResource}
                        onContextMenu={this.onContextMenu} />
                </div>
                <UnitPriceSpecification
                    unitPriceId={this.props.item.id}
                    specificationItems={this.props.specificationItems}
                    createUnitPriceSpecification={this.props.createUnitPriceSpecification}
                    deleteUnitPriceSpecification={this.props.deleteUnitPriceSpecification}
                    onContextMenu={this.onContextMenu}/>
            </div>
        )
    }
}

export default UnitPriceCalculation;