import { Component, ReactNode } from "react"
import style from './Task.module.css';
import { TaskInfo } from "./TaskContainer";

type Props = {
    item: TaskInfo
}

class Task extends Component<Props> {
    render(): ReactNode {
        const task = this.props.item;
        return (
            <div className={style.taskContainer}>
                <div className={style.taskName}>{task.name}</div>
                <div>{task.description}</div>
                <div>Модуль: {task.module}</div>
                <div>Статус: {task.status}</div>
                <div>Начало: {task.startDate}</div>
                <div>Дедлайн: {task.deadline}</div>
            </div>
        )
    }
}

export default Task;