import { Component, ReactNode } from "react";
import style from './UnitPrice.module.css';
import { ModalMenuOption } from "../common/modalMenu/ModalMenu";
import { HiBan, HiCheck } from 'react-icons/hi';
import { ResourceTypes, UnitPriceResourceInfo, UnitPriceResourceRequest } from "../../store/types/unit-price/UnitPriceInfo";

type Props = {
    unitPriceId: number,
    resourceItems: UnitPriceResourceInfo[],
    onContextMenu: (top: number, left: number, options: Array<ModalMenuOption>) => void,
    createUnitPriceResource: (unitPriceId: number, resource: UnitPriceResourceRequest) => void,
    deleteUnitPriceResource: (unitPriceId: number, id: number) => void,
}

class UnitPriceSummary extends Component<Props> {
    state = {
        isAddMode: false
    };

    onContextMenu = (event: React.MouseEvent<HTMLElement>) => {
        event.preventDefault();

        const options : Array<ModalMenuOption> = [
            {name: "Добавить ресурс", onClick: this.modalMenuOnInsert}
        ];

        this.props.onContextMenu(event.pageY, event.pageX, options);
    }

    modalMenuOnInsert = () => {
        this.setState({isAddMode: true});
    }

    onNewItemSubmit = (request: UnitPriceResourceRequest) => {
        this.props.createUnitPriceResource(this.props.unitPriceId, request);
        this.setState({isAddMode: false});
    }

    onNewItemCancel = () => {
        this.setState({isAddMode: false});
    }

    render(): ReactNode {
        let index = 1;
        return (
            <div className={style.upeSummary}>
                <div className={style.upeSummaryHeader} onContextMenu={this.onContextMenu}>
                    <div className={style.upeSummaryCode}>№ п/п</div>
                    <div className={style.upeSummaryName}>Наименование ресурса</div>
                    <div className={style.upeSummaryUnits}>Тип ресурса</div>
                    <div className={style.upeSummaryWorkerLaborCost}>ТЗР</div>
                    <div className={style.upeSummaryMachineLaborCost}>ТЗМ</div>
                </div>
                {
                    this.props.resourceItems.map(resource => 
                        <UnitPriceSummaryItem 
                            key={resource.id}
                            deleteUnitPriceResource={this.props.deleteUnitPriceResource}
                            onContextMenu={this.props.onContextMenu}
                            modalMenuOnInsert={this.modalMenuOnInsert}
                            resource={resource} 
                            index={index++} />)
                }
                {
                    this.state.isAddMode &&
                        <AddUnitPriceSummaryItem 
                            onNewItemSubmit={this.onNewItemSubmit}
                            onCancel={this.onNewItemCancel}
                            index={index++} />
                }
            </div>
        );
    }
}

type Props2 = {
    index: number,
    onNewItemSubmit: (request: UnitPriceResourceRequest) => void,
    onCancel: () => void
}

class AddUnitPriceSummaryItem extends Component<Props2>{
    state = {
        type: ResourceTypes.Machine,
        name: "",
        workerLaborCostPerHour: 0,
        machineLaborCostPerHour: 0,
    }

    getResourceType = (type: ResourceTypes) => {
        switch (type){
            case ResourceTypes.Machine:
                return "МиМ";
            case ResourceTypes.Worker:
                return "ОР";
            default:
                return "-";
        }
    }

    onTypeChanged = (event: React.ChangeEvent<HTMLSelectElement>) => {
        this.setState({type: Number.parseInt(event.target.value)});
    }

    onSubmit = () => {
        const request: UnitPriceResourceRequest = {
            name: this.state.name,
            type: this.state.type,
            workerLaborCostPerHour: this.state.workerLaborCostPerHour,
            machineLaborCostPerHour: this.state.machineLaborCostPerHour
        }
        
        this.props.onNewItemSubmit(request);
    }

    onNameChange = (event: React.ChangeEvent<HTMLInputElement>) => this.setState({name: event.target.value});

    onWorkerLaborChange = (event: React.ChangeEvent<HTMLInputElement>) => this.setState({workerLaborCostPerHour: event.target.value});

    onMachineLaborChange = (event: React.ChangeEvent<HTMLInputElement>) => this.setState({machineLaborCostPerHour: event.target.value});
    
    render(): ReactNode {
        return (
            <div className={style.upeSummaryData}>
                <div className={style.upeSummaryCode}>{this.props.index}</div>
                <div className={style.upeSummaryName}>
                    <input className={style.upeSummaryNameInput} placeholder="Наименование" value={this.state.name} onChange={this.onNameChange} type={"text"} />
                </div>
                <div className={style.upeSummaryUnits}>
                    <select onChange={this.onTypeChanged} required>
                        <option value={ResourceTypes.Machine}>{this.getResourceType(ResourceTypes.Machine)}</option>
                        <option value={ResourceTypes.Worker}>{this.getResourceType(ResourceTypes.Worker)}</option>
                    </select>
                </div>
                <div className={style.upeSummaryWorkerLaborCost}>
                    {
                        this.state.type === ResourceTypes.Worker
                            ? <input className={style.upeSummaryCostInput} placeholder="0" value={this.state.workerLaborCostPerHour} onChange={this.onWorkerLaborChange} type={"text"} />
                            : "-"
                    }
                </div>
                <div className={style.upeSummaryMachineLaborCost}>
                    {
                        this.state.type === ResourceTypes.Machine
                            ? <input className={style.upeSummaryCostInput} placeholder="0" value={this.state.machineLaborCostPerHour} onChange={this.onMachineLaborChange} type={"text"} />
                            : "-"
                    }
                </div>
                <div className={style.upeControlPanel}>
                    <HiBan onClick={this.props.onCancel} className={style.upeControlPanelIcon}/>
                    <HiCheck onClick={this.onSubmit} className={style.upeControlPanelIcon} />
                </div>
            </div>
        );
    }
}

type Props1 = {
    deleteUnitPriceResource: (unitPriceId: number, id: number) => void,
    onContextMenu: (top: number, left: number, options: Array<ModalMenuOption>) => void,
    modalMenuOnInsert: () => void,
    resource: UnitPriceResourceInfo,
    index: number
}

class UnitPriceSummaryItem extends Component<Props1>{
    onContextMenu = (event: React.MouseEvent<HTMLElement>) => {
        event.preventDefault();

        const options : Array<ModalMenuOption> = [
            {name: "Добавить ресурс", onClick: this.props.modalMenuOnInsert},
            {name: "Редактировать ресурс", onClick: () => {}},
            {name: "Удалить ресурс", onClick: this.onDelete}
        ];

        this.props.onContextMenu(event.pageY, event.pageX, options);
    }

    onDelete = () => {
        this.props.deleteUnitPriceResource(this.props.resource.unitPriceId, this.props.resource.id);
    }

    getResourceType = () => {
        switch (this.props.resource.type){
            case ResourceTypes.Machine:
                return "МиМ";
            case ResourceTypes.Worker:
                return "ОР";
            default:
                return "-";
        }
    }

    render(): ReactNode {
        const resource = this.props.resource;
        return (
            <div className={style.upeSummaryData} onContextMenu={this.onContextMenu}>
                <div className={style.upeSummaryCode}>{this.props.index}</div>
                <div className={style.upeSummaryName}>{resource.name}</div>
                <div className={style.upeSummaryUnits}>{this.getResourceType()}</div>
                <div className={style.upeSummaryWorkerLaborCost}>{resource.type === ResourceTypes.Worker ? resource.workerLaborCostPerHour : "-"}</div>
                <div className={style.upeSummaryMachineLaborCost}>{resource.type === ResourceTypes.Machine ?resource.machineLaborCostPerHour : "-"}</div>
            </div>
        );
    }
}

export default UnitPriceSummary;